/* index.css */
body { 
	background: url(bgim.jpg) no-repeat;
	background-size: cover;
} 

.game-area { 
	background-color: rgb(0, 0, 0);
	opacity: 0.7;
	position: relative; 
	width: 420px; 
	height: 420px; 
	border: 2px solid #000000; 
	border-radius: 10px; 
	margin: 1px auto; 
	display: flex; 
	flex-wrap: wrap; 
	box-shadow: 0 0 10px #abbfc0; 
} 

@media only screen and (max-width: 800px) { 
	.game-area { 
		position: relative; 
		width: 350px; 
		height: 300px; 
	} 

	.snake { 
		width: 12px; 
		height: 12px; 
	} 
} 

.snake { 
	position: absolute; 
	width: 2%; 
	height: 2%; 
	background-color: #f4a108; 
	border: 2px solid white; 
	z-index: 2; 
} 

.food { 
	position: absolute; 
	width: 12px; 
	height: 12px; 
	background-color: white; 
	border-radius: 20px; 
	z-index: 1; 
}
