/* Menu.css */
.wrapper { 
	position: relative; 
	width: 200px; 
	height: 250px; 
	border: 6px solid #000000; 
	border-radius: 78px;
	padding: 4%;
	/* border-radius: 10px; */
	margin: 50px auto; 
	margin-top: 200px; 
	display: flex; 
	flex-wrap: wrap; 
	justify-content: center; 
	/* box-shadow: 0 0 10px #abbfc0; */
} 

.start { 
	margin: 100px; 
	background: #32975a; 
	color: rgb(255, 255, 255); 
	border-radius: 8px; 
	border:0px; 
	padding: 10px; 
	font-size: 1.6em; 
	box-shadow: 0 0 70px #080808; 
	font-family: "Courier New", Courier, monospace; 
}
